import React from 'react'
import { FiAlertCircle } from 'react-icons/fi'

const DeleteModal = (props) => {
  return (
    <>
                    <div class="p-6 text-center">
                        <div className='w-full flex h-10'> <span className='mx-auto'><FiAlertCircle size={30} /></span></div>
                        <h3 class="mb-5 2xl:text-lg text-md font-normal text-gray-500">do you want to delete this application ?</h3>
                        <div>
                        <button type="button" class="text-white bg-red-600 hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-red-300 font-medium rounded-lg text-sm inline-flex items-center px-5 py-2.5 text-center mr-2" onClick={() => props?.deleteSafFun()}>
                            Delete
                        </button>
                        <button onClick={props?.closeModal} type="button" class="text-white bg-green-600 hover:bg-green-800 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm inline-flex items-center px-5 py-2.5 text-center mr-2">
                            Cancel
                        </button>
                        </div>

                    </div>
    </>
  )
}

export default DeleteModal