import React, { useState } from 'react'
import { BiChevronsRight } from 'react-icons/bi'
import { useNavigate } from 'react-router-dom'
import {AiFillInfoCircle} from 'react-icons/ai'
import { nullToNA } from '../../../Components/Common/PowerUps/PowerupFunctions'

const ServicesAppCard = (props) => {

    const navigate = useNavigate()

  return (
    <>
    
    <div className='w-full pb-0 pt-10 bg-white rounded-lg border border-gray-100 shadow-xl mb-6 relative overflow-hidden animate__animated animate__fadeIn animate__faster'>
                                        <div className='w-full pb-4 pt-0 px-4'>
                                            <div className='w-full rounded-lg  space-b-2'>
                                                <div className='w-full  grid grid-cols-12'>
                                                    <div className='col-span-12 md:col-span-6 grid grid-cols-12'>
                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.applicant_name)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Applicant Name</div>
                                                        </div>

                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.apply_date) }</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Apply Date</div>
                                                        </div>
                                                    </div>
                                                   
                                                    <div className='col-span-12 md:col-span-6 grid grid-cols-12 mt-4 md:mt-0'>
                                                        {props?.label != 'Objection' && <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.application_no) }</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Application No. </div>
                                                        </div>}

                                                        {props?.label == 'Objection' && <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.objection_no)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Objection No. </div>
                                                        </div>}

                                                        {(props?.application?.pt_no == '' || props?.application?.pt_no == undefined) ? 
                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA((props?.application?.new_holding_no == '' || props?.application?.new_holding_no == undefined) ? props?.application?.holding_no : props?.application?.new_holding_no)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Holding No.</div>
                                                        </div> 
                                                        :
                                                        <div className='col-span-6'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.pt_no)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'>Property Tax No.</div>
                                                        </div>
                                                        }
                                                    </div>
                                                    {/* <div className='col-span-12 md:col-span-2 grid grid-cols-12 mt-4 md:mt-0'>
                                                        <div className='col-span-12'>
                                                            <div className='font-semibold text-gray-700 2xl:text-base text-sm'>{nullToNA(props?.application?.backToCitizen)}</div>
                                                            <div className='text-xs text-gray-600 font-serif italic'><abbr title="Back To Citizen" className='text-xs text-gray-600 font-serif italic no-underline'>BTC</abbr> </div>
                                                        </div>
                                                    </div> */}
                                                </div>
                                                
                                                {props?.application?.backToCitizen == true ?
                                                 <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                    Your application form has been redirected as <span className="font-semibold poppins">Back to Citizen (BTC) </span>to re-upload documents. We are waiting for you to upload documents. Thank You !!
                                                </div> : 
                                                <div className="w-full poppins 2xl:text-sm text-xs italic mt-4 text-gray-800 pr-10">
                                                Your {props?.label} application is pending at <span className='font-semibold poppins'> {props?.application?.pending_at} </span> and it is in process. Thank you for your patience !!
                                            </div>
                                                }
                                                 {props?.application?.backToCitizen == true &&
                                               <>
                                               <hr className='2xl:h-[0.2rem] 2xl:my-4 h-[0.1rem] my-2' />
                                               <div className='w-full pl-2 text-sm poppins'>
                                                    <div className='font-semibold 2xl:text-base text-md 2xl:mb-2 mb-1 poppins'>Action</div>
                                                <div className='flex flex-wrap items-center flex-row gap-8'>
                                                 <div className='2xl:text-sm text-xs text-amber-600 poppins'> <AiFillInfoCircle fontSize={14} className="inline" /> Document is pending <span className='float-right ml-4 cursor-pointer bg-indigo-500 text-white px-4  2xl:text-sm text-xs shadow-lg py-[0.2rem] hover:bg-indigo-600 hover:scale-105 rounded-sm border border-white' onClick={() => navigate(`/servicesDocUpload/${props?.application?.application_id}/${props?.label}`)}>Upload</span></div>
                                                </div>
                                                </div>
                                                </>}
                                                <span onClick={() => navigate(`/${props?.location}/${props?.application?.application_id}`)} className=' text-white px-2 2xl:px-4 py-[0.2rem] text-xs 2xl:text-sm md:absolute bottom-3 2xl:bottom-2 right-2 rounded-sm bg-indigo-500 border-indigo-400 hover:border-indigo-600 hover:bg-indigo-600 hover:text-white shadow-sm  cursor-pointer poppins md:mt-0 mt-2'>View Details<BiChevronsRight className="inline font-semibold text-xl" /></span>
                                                </div>
                                                {props?.label != 'Objection' ? <span className='px-2 text-xs absolute top-2 left-2  py-1 rounded-sm bg-amber-500 border-amber-300 hover:border-amber-600 hover:bg-amber-500 tracking-wider text-white font-semibold shadow-sm poppins'>{props?.label}</span> :
                       <span className='px-2 text-xs absolute top-2 left-2  py-1 rounded-sm bg-amber-500 border-amber-300 hover:border-amber-600 hover:bg-amber-500 tracking-wider text-white font-semibold shadow-sm poppins'>
                        {props?.application?.objection_for == 'Clerical Mistake' && <>Clerical Objection</>}
                        {props?.application?.objection_for == 'Assessment Error' && <>Assessment Objection</>}
                        {props?.application?.objection_for == 'Forgery' && <>Forgery Objection</>}
                        </span>                         
                                }
                                                </div>
                                                </div>

    </>
  )
}

export default ServicesAppCard